body {
    background: #0f1923;
    font-family: "Muli";
}

.button {
    display: -webkit-flex;
    display: flex;
    height: 50vh;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-flow: column;
            flex-flow: column;
}
.button a {
    width: 50%;
    max-width: 240px;
    height: 54px;
    padding: 8px;
    font-size: 0.8rem;
    font-weight: 900;
    color: #ff4655;
    text-align: center;
    text-transform: uppercase;
    text-decoration: none;
    box-shadow: 0 0 0 1px inset rgba(236, 232, 225, 0.3);
    position: relative;
    margin: 10px 0;
}
.button a.white:hover > p {
    color: #ece8e1;
}
.button a.white > p {
    background: #ece8e1;
    color: #0f1923;
}
.button a.white > p span.base {
    border: 1px solid transparent;
}
.button a.transparent:hover > p {
    color: #ece8e1;
}
.button a.transparent:hover > p span.text {
    box-shadow: 0 0 0 1px #ece8e1;
}
.button a.transparent > p {
    background: #0f1923;
    color: #ece8e1;
}
.button a.transparent > p span.base {
    border: 1px solid #ece8e1;
}
.button a:after,
.button a:before {
    content: "";
    width: 1px;
    position: absolute;
    height: 8px;
    background: #0f1923;
    left: 0;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.button a:before {
    right: 0;
    left: auto;
    left: initial;
}
.button a p {
    margin: 0;
    height: 54px;
    line-height: 54px;
    box-sizing: border-box;
    z-index: 1;
    left: 0;
    width: 100%;
    position: relative;
    overflow: hidden;
}
.button a p span.base {
    box-sizing: border-box;
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    left: 0;
    border: 1px solid #ff4655;
}
.button a p span.base:before {
    content: "";
    width: 2px;
    height: 2px;
    left: -1px;
    top: -1px;
    background: #0f1923;
    position: absolute;
    transition: 0.3s ease-out all;
}
.button a p span.bg {
    left: -5%;
    position: absolute;
    background: #ff4655;
    width: 0;
    height: 100%;
    z-index: 3;
    transition: 0.3s ease-out all;
    -webkit-transform: skewX(-10deg);
            transform: skewX(-10deg);
}
.button a p span.text {
    z-index: 4;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
}
.button a p span.text:after {
    content: "";
    width: 4px;
    height: 4px;
    right: 0;
    bottom: 0;
    background: #0f1923;
    position: absolute;
    /* transition: 0.3s ease-out all; */
    z-index: 5;
}
.button a:hover {
    color: #ece8e1;
}
.button a:hover span.bg {
    width: 110%;
}
.button a:hover span.text:after {
    background: #ece8e1;
}

